@font-face {
  font-family: "PT Mono";
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  margin: 0 auto;
  width: 100%;
  padding: 0 50px;
}
@media (max-width: 1023.9px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767.9px) {
  .container {
    padding: 0 25px;
  }
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(4, 24, 39, 0.8);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 145px 40px;
  transform: translateX(-8px);
}
@media (max-width: 767.9px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  background-color: #00b2ff;
  border-radius: 3px;
  width: 500px;
  padding: 40px;
}
@media (max-width: 767.9px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: #000000;
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767.9px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__button {
  position: absolute;
  right: 20px;
  top: 25px;
  border: none;
  background-color: #00b2ff;
  cursor: pointer;
}

.page-header {
  padding: 20px 0 38px;
  background: linear-gradient(180deg, #041827 84.9%, transparent 100%);
  position: sticky;
  top: 0;
  z-index: 5;
}
@media (max-width: 1023.9px) {
  .page-header {
    padding: 20px 70px 32px 50px;
  }
}
@media (max-width: 767.9px) {
  .page-header {
    padding: 10px 10px 26px 0;
  }
}

.page-header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  max-width: 1320px;
  padding-right: 110px;
}
@media (max-width: 1023.9px) {
  .page-header__container {
    max-width: 650px;
    padding: 0;
  }
}

.page-header__logo {
  padding-top: 10px;
}
@media (max-width: 1023.9px) {
  .page-header__logo {
    padding-top: 0;
  }
}
@media (max-width: 767.9px) {
  .page-header__logo {
    margin-top: -5px;
  }
}

.page-header__contacts-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  row-gap: 15px;
  column-gap: 30px;
  max-width: 480px;
}
@media (max-width: 1023.9px) {
  .page-header__contacts-list {
    justify-content: space-between;
    column-gap: 0;
    min-width: 100px;
    max-width: 300px;
    align-self: baseline;
  }
}

.page-header__contacts-item {
  min-width: 225px;
  display: flex;
  align-items: center;
}
@media (max-width: 1023.9px) {
  .page-header__contacts-item {
    justify-content: center;
    min-width: auto;
    width: 40px;
    height: 40px;
  }
}

.page-header__contacts-link {
  display: flex;
  align-items: center;
  column-gap: 16px;
  color: #f2f5f6;
  font-family: "PT Mono", "Times New Roman", serif;
  font-size: 16px;
  line-height: 18px;
  padding-left: 5px;
}
.page-header__contacts-link:active svg {
  opacity: 0.6;
}
@media (hover: hover) {
  .page-header__contacts-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.page-header__contacts-link.focus-visible:focus {
  opacity: 0.8;
}

.page-header__contacts-link span {
  align-self: baseline;
}
@media (max-width: 1023.9px) {
  .page-header__contacts-link span {
    display: none;
  }
}

.page-header__button {
  color: #f2f5f6;
  background-color: #041827;
  width: 160px;
  min-height: 40px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  padding: 10px 10px 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  line-height: 17px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
}
@media (max-width: 1023.9px) {
  .page-header__button {
    display: none;
  }
}
@media (hover: hover) {
  .page-header__button:not(:disabled):hover:not(.focus-visible) {
    border: 1px solid #00b2ff;
    color: #00b2ff;
  }
}
.page-header__button:not(:disabled).focus-visible:focus {
  border: 1px solid #00b2ff;
  color: #00b2ff;
}
.page-header__button:active {
  opacity: 0.9;
}
.page-header__button:disabled {
  opacity: 0.2;
}

.logo {
  max-width: 215px;
}
@media (max-width: 767.9px) {
  .logo {
    width: 35px;
    height: auto;
  }
}
@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.logo.focus-visible:focus {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.promo {
  background-color: #041827;
  background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, transparent 100%), url("../img/promo-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -120px;
  max-width: 100%;
  padding: 301px 0 277px;
  color: #f2f5f6;
  min-height: 870px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .promo {
    background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, transparent 100%), url("../img/promo-background@2x.jpg");
  }
}
@media (max-width: 1023.9px) {
  .promo {
    padding: 192px 0 205px 0;
    background-position: 21% 15%;
    min-height: 640px;
  }
}
@media (max-width: 767.9px) {
  .promo {
    margin-top: -108px;
    padding: 182px 0 190px;
    min-height: 560px;
    background-position: 25% 60%;
  }
}

.promo__container {
  max-width: 1320px;
  padding-left: 175px;
}
@media (max-width: 1023.9px) {
  .promo__container {
    padding-left: 50px;
  }
}

.promo h2 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 6px;
  line-height: 94px;
  font-size: 80px;
  margin-bottom: 30px;
}
@media (max-width: 1023.9px) {
  .promo h2 {
    letter-spacing: 5px;
    line-height: 75px;
    font-size: 64px;
    margin-bottom: 3px;
  }
}
@media (max-width: 767.9px) {
  .promo h2 {
    font-size: 36px;
    line-height: 42px;
    letter-spacing: 2.5px;
    margin-bottom: 8px;
  }
}

.promo p {
  margin: 0;
  line-height: 34px;
  font-size: 22px;
  font-family: "PT Mono", "Times New Roman", serif;
  width: 685px;
  margin-bottom: 47px;
}
.promo p br {
  display: none;
}
@media (max-width: 1023.9px) {
  .promo p {
    margin-bottom: 55px;
    width: auto;
  }
  .promo p br {
    display: block;
  }
}
@media (max-width: 767.9px) {
  .promo p {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 43px;
    min-width: 271px;
    max-width: 100%;
  }
  .promo p br {
    display: none;
  }
}

.promo__button {
  display: inline-block;
  padding: 19px 20px;
  width: 410px;
  min-height: 55px;
  background-color: #00b2ff;
  color: #f2f5f6;
  border-radius: 3px;
}
@media (max-width: 1023.9px) {
  .promo__button {
    padding: 15px 20px;
    margin-left: 45px;
  }
}
@media (max-width: 767.9px) {
  .promo__button {
    margin-left: 0;
    padding: 13px 20px;
    width: 270px;
    min-height: 40px;
  }
}
@media (hover: hover) {
  .promo__button:hover:not(.focus-visible) {
    opacity: 0.9;
  }
}
.promo__button.focus-visible:focus {
  opacity: 0.9;
}

.promo__button span {
  display: block;
  text-align: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 1px;
  line-height: 21px;
}
@media (max-width: 767.9px) {
  .promo__button span {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 1px;
  }
}
@media (max-width: 767.9px) {
  .promo__button span:first-child {
    display: none;
  }
}
.promo__button span:last-child {
  display: none;
}
@media (max-width: 767.9px) {
  .promo__button span:last-child {
    display: block;
  }
}

.advantages {
  background-color: #f8fdff;
  padding: 121px 0 60px 0;
}
@media (max-width: 1023.9px) {
  .advantages {
    padding: 80px 0 40px;
  }
}
@media (max-width: 767.9px) {
  .advantages {
    padding: 50px 0 25px;
  }
}

.advantages__container {
  max-width: 1320px;
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 30px;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 1023.9px) {
  .advantages__list {
    row-gap: 30px;
  }
}
@media (max-width: 767.9px) {
  .advantages__list {
    row-gap: 30px;
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  min-height: 300px;
  padding: 38px 45px 35px 41px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
}
@media (max-width: 1023.9px) {
  .advantages__item {
    min-width: 320px;
    min-height: 360px;
    padding: 40px 30px 30px 40px;
  }
}
@media (max-width: 767.9px) {
  .advantages__item {
    min-width: 270px;
    min-height: 340px;
    padding: 25px 30px 35px 25px;
  }
}
@media (hover: hover) {
  .advantages__item:hover:not(.focus-visible) {
    background-color: #00b2ff;
    border-color: none;
  }
  .advantages__item:hover:not(.focus-visible) h3,
.advantages__item:hover:not(.focus-visible) p {
    color: #f2f5f6;
  }
  .advantages__item:hover:not(.focus-visible) svg {
    fill: #f2f5f6;
