.page-footer__nav-list,
.page-footer__contacts-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.page-footer__nav-list {
  width: 470px;
  column-width: 220px;
  column-count: 2;
  column-gap: 30px;
}
.page-footer__nav-list::after {
  content: "";
  display: block;
  height: 2em;
}
@media (max-width: 1023.9px) {
  .page-footer__nav-list {
    column-count: 1;
    flex-direction: column;
    column-gap: 0;
    row-gap: 5px;
  }
}
@media (max-width: 767.9px) {
  .page-footer__nav-list {
    padding: 0 26px 0 25px;
    max-height: max-content;
  }
}

.page-footer__contacts {
  max-width: 345px;
}
@media (max-width: 1023.9px) {
  .page-footer__contacts {
    padding-left: 21px;
  }
}
@media (max-width: 767.9px) {
  .page-footer__contacts {
    width: 100%;
    max-width: 100%;
    padding: 0;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

@media (max-width: 767.9px) {
  .page-footer__contacts-list {
    padding: 0 26px 0 25px;
    max-height: none;
  }
}

.page-footer__contacts-item {
  margin-bottom: 7px;
}
.page-footer__contacts-item:last-child {
  margin-bottom: 15px;
}

.page-footer__button {
  border: none;
  background-color: inherit;
  padding: 0;
  text-align: left;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 0.2px;
}
@media (max-width: 1023.9px) {
  .page-footer__button {
    letter-spacing: 0.7px;
    font-size: 15px;
    line-height: 16px;
    margin-bottom: 24px;
  }
}
@media (max-width: 767.9px) {
  .page-footer__button {
    margin-bottom: 0;
    width: 100%;
    padding: 23px 26px 19px 25px;
  }
}

.page-footer__contacts-item address {
  cursor: pointer;
}
@media (max-width: 767.9px) {
  .page-footer__contacts-item address {
    padding-bottom: 5px;
  }
}

.page-footer__nav-item a,
.page-footer__contacts-item a,
.page-footer__contacts-item address {
  font-family: "PT Mono", "Times New Roman", serif;
  font-style: normal;
  color: rgba(255, 255, 255, 0.7);
  line-height: 22px;
}
@media (max-width: 767.9px) {
  .page-footer__nav-item a,
.page-footer__contacts-item a,
.page-footer__contacts-item address {
    font-size: 14px;
    line-height: 19px;
  }
}
@media (hover: hover) {
  .page-footer__nav-item a:hover:not(.focus-visible),
.page-footer__contacts-item a:hover:not(.focus-visible),
.page-footer__contacts-item address:hover:not(.focus-visible) {
    color: #f2f5f6;
  }
}
.page-footer__nav-item a.focus-visible:focus,
.page-footer__contacts-item a.focus-visible:focus,
.page-footer__contacts-item address.focus-visible:focus {
  color: #f2f5f6;
}
.page-footer__nav-item a:active,
.page-footer__contacts-item a:active,
.page-footer__contacts-item address:active {
  color: #f2f5f6;
  opacity: 0.9;
}

@media (max-width: 767.9px) {
  .accordion .page-footer__button {
    background-image: url("../img/svg/plus.svg");
    background-repeat: no-repeat;
    background-position: 91.5% center;
  }
  .accordion .is-active .page-footer__button {
    background-image: url("../img/svg/minus.svg");
  }
}

